.nav{
    display: flex;
    width: 100%;
    gap: 20px;
    flex-wrap: wrap;
    height: 40px;
}

.nav_link{
    text-decoration: none;
    color: var(--text-color);
    transition: 0.3s ease;
}

.nav_link:hover{
    scale: 1.1;
    font-weight: bold;
}

.current_route{
    color: var(--text-color-2);
}
