.App {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: space-around;
    justify-content: space-around;
}

.content{
    display: flex;
    height: calc(100% - 200px); /* Adjust for header height */
    min-height: max-content;
    width: 90%;
    margin: 0 auto;
    justify-content: space-around;
}

.main_frame{
    width: 75%;
}

.content > Aside{
    width: 20%;
}